.contacts {

  @include tabletScreen{
    background-position-y: 100px;
    background-size: 90%;
  }

  @include phoneScreen{
  }

  &__title {
    font-weight: 800;
    font-size: 70px;
    line-height: 90px;
    text-transform: uppercase;
    text-align: center;
    color: #212121;

    @include tabletScreen{
      font-size: 48px;
      line-height: 59px;
    }

    @include phoneScreen{
      font-size: 28px;
      line-height: 40px;
    }
  }

  &__text {
    margin: 30px auto 0;
    max-width: 1000px;
    text-align: center;
    font-weight: 300;
    font-size: 30px;
    line-height: 54px;
    color: color(black);

    @include tabletScreen{
      margin-top: 16px;
      max-width: 680px;
      font-size: 20px;
      line-height: 32px;
    }

    @include phoneScreen{
      font-size: 16px;
      line-height: 24px;
    }
  }

  &__form {
    margin: 30px auto 80px;
    max-width: 532px;

    @include phoneScreen{
      margin: 16px auto 48px;
    }
  }

  input{
    padding: 8px 0 8px 8px;
    border: none;
    outline: none;
    font-size: 18px;
    color: #212121;
    border-bottom: 1px solid #BCBDC0;
    font-family: inherit;
    transition: border .6s;

    &::placeholder{
      color: #6C6D70;
    }

    &:focus{
      border-width: 2px;
      border-color: #010A08;
    }

    &:active{
      border-bottom: 2px solid #BCBDC0;
    }

    &.error{
      border-color: red;
    }

    @include phoneScreen{
      font-size: 16px;
    }
  }

  &__form-name {
    width: 100%;
  }

  &__form-email-box {
    margin: 36px 0 32px;
    display: flex;
    justify-content: space-between;
    align-items: center;

    @include phoneScreen{
      margin: 24px 0;
      display: block;

      input:first-child{
        margin-bottom: 24px;
      }
    }
  }

  &__form-email,&__form-tel {
    width: 256px;

    @include phoneScreen{
      width: 100%;
    }
  }

  textarea{
    width: 100%;
    height: 128px;
    padding: 8px;
    border: 1px solid #BCBDC0;
    font-size: 18px;
    line-height: 24px;
    color: #000;
    transition: border .6s;

    &::placeholder{
      color: #6C6D70;
    }

    &:focus{
      border-width: 2px !important;
      border-color: red;
    }

    @include phoneScreen{
      font-size: 16px;
      line-height: 22px;
    }
  }

  &__form-checkbox {
    margin: 24px 0 70px;
    display: flex;
    align-items: center;
    
    input {
      width: 0.1px;
      height: 0.1px;
      opacity: 0;
      visibility: hidden;
      position: absolute;
      z-index: -1;
    }

    label {
      padding-left: 40px;
      font-size: 16px;
      font-weight: 300;
      line-height: 24px;
      color: #010A08;
      cursor: pointer;
      position: relative;

      &::before {
        content: "";
        position: absolute;
        left: 0;
        width: 24px;
        height: 24px;
        border: 2px solid #212121;
      }

      &.error{

        &::before{
          border-color: red;
        }
      }

      &::after {
        display: none;
        content: "";
        position: absolute;
        left: 3px;
        top: 4px;
        width: 16.67px;
        height: 16px;
        background: url("../assets/images/checkbox.svg") no-repeat;
      }
    }

    input:checked + label {

      &::before {
        background-color: #212121;
        border-color: #010A08 !important;
      }

      &::after {
        display: block;
        z-index: 2;
      }
    }

    @include phoneScreen{
      margin: 24px 0 45px;

      label{
        font-size: 12px;
        line-height: 15px;
        padding-left: 36px;
  
        &::before {
          top: 50%;
          transform: translateY(-50%);
        }
  
        &::after {
          top: 50%;
          transform: translateY(-50%);
        }
      }
    }
  }

  &__form-button {
    width: 100%;
    height: 73px;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 4px solid #000;
    font-weight: 700;
    font-size: 20px;
    line-height: 40px;
    color: #212121;

    &:hover{
      color: color(white);
      background-color: #000;
    }

    @include phoneScreen{
      border-width: 2px;
      font-size: 16px;
      height: 64px;
    }
  }

  &__footer {
    width: 100%;
    white-space: nowrap;
    overflow: hidden;
    padding: 22px 0;
    background-color: rgb(0, 141, 0);

    @keyframes marquee {
      0%   { transform: translate(0, 0); }
      100% { transform: translate(-100%, 0); }
    }

    span{
      font-weight: 800;
      font-size: 40px;
      line-height: 49px;
      letter-spacing: 0.04em;
      text-transform: uppercase;
      color: #212121;
      display: inline-block;
      padding-left: 100%;
      animation: marquee 50s linear infinite;

      &:hover{
        animation-play-state: paused;
      }
    }

    @include tabletScreen{
      padding: 15px 0;
    }

    @include phoneScreen{
      span{
        font-size: 32px;
        line-height: 39px;
      }
    }
  }
}