.header {
  background-color: #000000;
  position: fixed;
  width: 100%;
  z-index: 5;

  &__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 0;

    @include ipadScreen {
      padding: 5px 0;
    }
  }

  &__logo {
    width: 70px;
    height: 70px;
    display: block;

    img {
      width: 100%;
      height: 100%;
    }
    @include ipadScreen {
      width: 60px;
      height: 60px;
    }
  }

  // &__navigation {}
  &__mobile-menu {
    display: none;

    @include ipadScreen {
      display: block;
      position: relative;
      width: 28px;
      height: 17px;
      z-index: 99;

      &::before, &::after, span {
        content: '';
        position: absolute;
        left: 0;
        width: 100%;
        height: 2px;
        background-color: rgb(255, 255, 255);
      }

      &::before {
        top: 0;
      }
      &:after {
        bottom: 0;
      }
      span {
        top: 7.5px;
      }

      &.active {

        span {
          background-color: rgb(219, 204, 204);
          opacity: 0;
          visibility: hidden;
          transition: all 0.2s;
        }

        &::before{
          background-color: rgb(219, 204, 204);
          top: 8px;
          transform: rotate(45deg);
          transition: all 0.2s;
        }

        &::after{
          background-color: rgb(219, 204, 204);
          bottom: 6px;
          transform: rotate(-45deg);
          transition: all 0.2s;
        }
      }
    }
  }
  @include phoneScreen {
    &__mobile-menu {
      width: 22px;
      height: 15px;

      span {
        top: 6.5px;
      }
      
      &.active:before{
        top: 7px;
      }
    }
  }

  &__menu {
    display: flex;
    align-items: center;

    @include ipadScreen {
      position: absolute;
      left: -1100%;
      top: 70px;
      width: 100%;
      padding: 45px 0 1000px;
      display: block;
      text-align: left;
      background-color: rgba(19, 28, 8, 0.5);
      backdrop-filter: blur(3px);
      transition: 0.3s;
      z-index: 10;

      &.active {
        left: 0;
        transition: 0.3s;
      }

      &:after {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 70%;
        height: 100%;
        background-color: color(white);
        z-index: -1;
        transition: 0.3s;
      }
    }
  }

  &__list {
    margin-left: 60px;

    &:first-child {
      margin-left: 0;
    }

    @include ipadScreen {
      padding-left: 20px;
      margin: 0 0 15px 0;
      position: relative;

      &:after {
        content: "";
        position: absolute;
        margin: 0 0 0 10px;
        top: 50%;
        width: 0px;
        height: 2px;
        background: #660A68;
        transition: 0.2s;
      }
  
      &:hover:after {
        width: 6%;
        transform: translationX(100%);
      }
    }
    @include phoneScreen {
      padding-left: 15px;
    }
    @include SEScreen {
      padding-left: 10px;
    }
  }

  &__link {
    color: #fff;
    font-size: 18px;
    font-weight: 400;

    &:hover {
      color: rgb(139, 139, 139);
      transition: all 0.3s;
    }

    @include ipadScreen {
      color: rgb(0, 0, 0);
      font-weight: 500;
      
      &:hover {
        color: rgb(94, 89, 89);
      }
    }
  }
}
