.paralax {
  
  .arrow {
    position: absolute;
    right: 12px;
    top: 24px;
    width: 0;
    height: 0;
    border-top: 6px solid rgb(0, 0, 0);
    border-left: 6px solid transparent;
    border-right: 6px solid transparent;

    &.active {
      transform: rotate(-180deg);
      transition: 0.3s;
    }
  }

  &__menu-arrow {
  }

  &__submenu-arrow.arrow {
    position: absolute;
    right: 10px;
    top: 10px;
    border-left: 7px solid #fff;
    border-top: 7px solid transparent;
    border-bottom: 7px solid transparent;
    border-right: none;

    @include ipadScreen {
      position: absolute;
      right: 8px;
      top: 15px;
      width: 0;
      height: 0;
      border-top: 6px solid rgb(255, 255, 255);
      border-left: 6px solid transparent;
      border-right: 6px solid transparent;
      border-bottom: none;
    }
  }

  &__munu li {
    position: relative;
  }

  &__menu-list {
    display: flex;
    justify-content: center;
    padding: 20px 0 230px 0;

    @include ipadScreen {
      padding: 20px 0 250px 0
    }
    @include phoneScreen{
      padding: 20px 0 250px 0
    }
  }

  &__menu-list>li {
    margin: 0 20px 0 0;
  }

  &__menu-link {
    width: 100%;
    height: 55px;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 55px 0 25px;
    border: 2px solid #8bc63e;
    font-weight: 700;
    font-size: 16px;
    line-height: 40px;
    color: #000000;
    background-color: #e0ffb7;
    cursor: pointer;

    &:hover {
      transition: all 0.2s;
    }
  }

  &__submenu-list {
    display: none;
    position: absolute;
    left: 0;
    top: 56px;
    padding: 2px 0 0 0;
    min-width: 230px;
    cursor: pointer;

    &.active {
      display: block;
      transition: all 0.2s;
    }
  }

  &__submenu-list>li {
    box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.25);
    background-color: #000;
  }

  &__submenu-link {
    color: rgb(255, 255, 255);
    display: inline-block;
    padding: 5px 10px;

    &:hover {
      color: navajowhite;
      transition: 0.2s;
    }
  }

  &__bottom-menu {
    display: none;
    position: absolute;
    top: 0;
    left: 100%;
    margin-left: 3px;
    background-color: rgb(95, 65, 65);
    box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.25);
    cursor: pointer;

    &.active {
      display: block;
    }

    @include ipadScreen {
      position: relative;
      left: 0;
      margin: 0 3px;
    }
    
    @include phoneScreen {
      position: relative;
      left: 0;
    }
  }

  &__bottom-menu>li {
    padding: 5px 5px;
    min-width: 170px;
    cursor: pointer;
  }

  &__bottom-link {
    padding: 5px 10px;
    color:rgb(161, 155, 155);
    font-weight: 500;
    font-size: 15px;
    cursor: pointer;

    &:hover {
      color: rgb(255, 255, 255);
      transition: 0.2s;
    }
  }
}
.popup__button {
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  background-color: rgb(69, 189, 0);
  padding: 12px 10px;
  width: 300px;
  border-radius: 5px;
  font-weight: 600;
}
.popup__button:hover {
  background-color: rgb(0, 12, 121);
  transition: all 0.3s;
}
