.tabs {
  margin: 100px 0;

  &__navigation {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;

    @include ipadScreen {
      display: flex;
      justify-content: space-around;
    }
  }

  &__list {
    list-style: none;

    @include ipadScreen {
      margin-bottom: 12px;
    }
    @include phoneScreen {
      width: 80%;
    }
  }
  &__link {
    width: 250px;
    height: 55px;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 20px;
    border: 2px solid #000;
    font-weight: 700;
    font-size: 16px;
    line-height: 40px;
    color: #212121;

    &:hover {
      background-color: rgb(0, 0, 0);
      transition: 0.3s;
      color: #fff;
    }

    &.active {
      background-color: #000;
      color: #fff;
    }

    @include phoneScreen {
      width: 100%;
    }
  }
  &__body {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    margin-top: 50px;

    @include phoneScreen {
      justify-content: space-around;
    }
  }
  &__body-list {
    background-color: rgb(104, 87, 87);
    border-radius: 50%;
    width: 200px;
    height: 200px;

    @include phoneScreen {
      margin-bottom: 20px;
    }

    img {
      display: block;
      width: 100%;
      height: 100%;
      border-radius: 50%;
    }
  }
  .twos__block {
    background-color: rgb(0, 88, 0);
    border-radius: 50%;
    width: 180px;
    height: 180px;
  }
  .tree__block {
    background-color: rgb(26, 24, 109);
    border-radius: 50%;
    width: 200px;
    height: 200px;
  }
  .four__block {
    background-color: rgb(119, 9, 9);
    border-radius: 50%;
    width: 150px;
    height: 150px;
  }
  .hide{
    display: none;
  }
}
