
.popup {
  position: fixed;
  width: 100%;
  height: 100%;
  background: rgba(19, 28, 8, 0.5);
  backdrop-filter: blur(3px);
  top: 0;
  left: 0;
  z-index: 99;
  opacity: 0;
  overflow-y: auto;
  overflow-x: hidden;
  visibility: hidden;
  transition: all 0.4s;
  align-items: center;
  justify-content: center;

  &.open {
    opacity: 1;
    visibility: visible;
  }

  &__body {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 16px;
    height: 100%;

    @include phoneScreen{
      align-items: start;
    }
  }

  &__content {
    max-width: 470px;
    padding: 48px 24px 56px;
    opacity: 0;
    transform: perspective(600px) translate(0, -100%) rotateX(45deg);
    transition: all 0.4s;
    background-color: color(white);
    position: relative;

    @include phoneScreen{
      padding: 45px 16px;
    }
  }

  &.open &__content {
    opacity: 1;
    transform: perspective(600px) translate(0px, 0%) rotateX(0);
  }

  .close-popup {
    position: absolute;
    top: 20px;
    right: 15px;
    line-height: 18px;
    font-size: 50px;
    color: #E6E7E9;
    cursor: pointer;
  }

  &__heading{
    font-weight: 800;
    font-size: 32px;
    line-height: 39px;
    text-align: center;
    text-transform: uppercase;
    color: color(black);
  }

  &__text{
    margin-top: 16px;
    font-weight: 300;
    font-size: 14px;
    line-height: 22px;
    text-align: center;
    color: color(black);
  }

  form{

    input{
      width: 100%;
      padding: 8px;
      margin-top: 24px;
      border: none;
      border-bottom: 1px solid #BCBDC0;
      color: color(black);
      font-size: 14px;

      &::placeholder{
        color: #6C6D70;
      }

      &:focus{
        border-width: 2px;
        border-color: color(lightgreen2);
      }

      &.error{
        border-color: red;
      }
    }

    .email-tel-box {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-bottom: 24px;

      input{
        width: 204px;
      }

      @include phoneScreen{
        display: block;

        input{
          width: 100%;
        }
      }
    }

    textarea{
      width: 100%;
      height: 81px;
      padding: 8px;
      color: color(black);
      font-size: 14px;
      line-height: 22px;
      border: 1px solid #BCBDC0;

      &::placeholder{
        color: #6C6D70;
      }

      &:focus{
        border-width: 2px !important;
        border-color: color(lightgreen2);
      }
    }

    .inputfile-box{
      border: none;
      padding: 0;
      margin: 0;
      position: relative;

      label {
        width: 280px;
        margin: 20px 0 8px;
        padding: 8px;
        border: none;
        cursor: pointer;
        color: #6C6D70;
        font-size: 14px;
        border-bottom: 1px solid #BCBDC0;
        text-overflow: ellipsis;
        position: relative;

        &:focus{
          border-bottom: 2px solid color(lightgreen2);
        }
    
        &::before {
          content: "Прикрепить";
          position: absolute;
          padding: 5px 0 0 30px;
          right: -48px;
          bottom: 4px;
          width: 24px;
          height: 24px;
          font-size: inherit;
          font-weight: 500;
          color: color(black);
          background-size: cover;
          // background: url('../assets/images/upload-icon.svg') no-repeat;
        }
      }
    
      input {
        margin: 0 !important;
        padding: 0;
        width: 0.1px;
        height: 0.1px;
        opacity: 0;
        visibility: hidden;
      }

      .file-cleaner{
        position: absolute;
        top: 30px;
        right: 145px;
        width: 14px;
        height: 14px;
        cursor: pointer;
        // background: url('../assets/images/file-cleaner.svg') no-repeat;
        background-size: contain;
        z-index: 2;
      }

      p{
        font-size: 11.3px;
        line-height: 17px;
        color: #BCBDC0;
      }

      @include phoneScreen{
        label{
          width: 88%;
          &::after {
            display: none;
          }
  
          &::before {
            content: '';
            bottom: 3px;
            right: -17%;
          }
        }

        .file-cleaner{
          right: 40px;
        }
      }
    }

    .checkbox-box {
      margin: 18px 0 48px;
      display: flex;
      align-items: center;
      
      input {
        width: 0.1px;
        height: 0.1px;
        opacity: 0;
        visibility: hidden;
        position: absolute;
        z-index: -1;
      }
  
      label {
        padding-left: 37px;
        font-size: 16px;
        font-weight: 400;
        font-size: 14px;
        line-height: 22px;
        color: color(black);
        cursor: pointer;
        position: relative;
  
        &::before {
          content: "";
          position: absolute;
          left: 0;
          width: 24px;
          height: 24px;
          border: 2px solid color(black);
        }
  
        &.error{
  
          &::before{
            border-color: red;
          }
        }
  
        &::after {
          display: none;
          content: "";
          position: absolute;
          left: 3px;
          top: 4px;
          width: 16.67px;
          height: 16px;
          // background: url("../assets/images/checkbox.svg") no-repeat;
        }
      }
  
      input:checked + label {
  
        &::before {
          background-color: color(black);
          border-color: color(black) !important;
        }
  
        &::after {
          display: block;
          z-index: 2;
        }
      }
  
      @include phoneScreen{
        margin: 24px 0 45px;
  
        label{
          font-size: 12px;
          line-height: 15px;
          padding-left: 36px;
    
          &::before {
            top: 50%;
            transform: translateY(-50%);
          }
    
          &::after {
            top: 50%;
            transform: translateY(-50%);
          }
        }
      }
    }

    .submit{
       width: 100%;
       height: 54px;
       display: flex;
       justify-content: center;
       align-items: center;
       border: 2px solid color(black);
       font-weight: 700;
      font-size: 18px;
      line-height: 22px;
      color: color(black);

      &:hover{
        background-color: color(black);
        color: color(white);
      }
    }
  }
}

.success{
  display: none;
  position: fixed;
  top: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  background: rgba(27, 35, 50, 0.6);
  backdrop-filter: blur(5px);
  z-index: 99;

  &.sended{
    display: block;
  }

  &__popup-wrapper{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px;
  }

  &__popup-content {
    max-width: 600px;
    width: 100%;
    height: 300px;
    padding: 30px 10px 0;
    background-color: color(white);
    margin: auto;
    border-radius: 20px;

    @include phoneScreen{
      height: 230px;
    }

  }

  &__content-text{
    margin-top: 30px;
    text-align: center;
    font-weight: 600;
    font-size: 30px;
    color: rgb(7, 136, 25);

    @include phoneScreen{
      margin-top: 10px;
      font-size: 23px;
    }
  }

  .success-checkmark {
    width: 80px;
    height: 115px;
    margin: 0 auto;
    
    .check-icon {
        width: 80px;
        height: 80px;
        position: relative;
        border-radius: 50%;
        box-sizing: content-box;
        border: 4px solid #4CAF50;
        
        &::before {
            top: 3px;
            left: -2px;
            width: 30px;
            transform-origin: 100% 50%;
            border-radius: 100px 0 0 100px;
        }
        
        &::after {
            top: 0;
            left: 30px;
            width: 60px;
            transform-origin: 0 50%;
            border-radius: 0 100px 100px 0;
            animation: rotate-circle 4.25s ease-in;
        }
        
        &::before, &::after {
            content: '';
            height: 100px;
            position: absolute;
            background: #FFFFFF;
            transform: rotate(-45deg);
        }
        
        .icon-line {
            height: 5px;
            background-color: #4CAF50;
            display: block;
            border-radius: 2px;
            position: absolute;
            z-index: 10;
            
            &.line-tip {
                top: 46px;
                left: 14px;
                width: 25px;
                transform: rotate(45deg);
                animation: icon-line-tip 0.75s;
            }
            
            &.line-long {
                top: 38px;
                right: 8px;
                width: 47px;
                transform: rotate(-45deg);
                animation: icon-line-long 0.75s;
            }
        }
        
        .icon-circle {
            top: -4px;
            left: -4px;
            z-index: 10;
            width: 80px;
            height: 80px;
            border-radius: 50%;
            position: absolute;
            box-sizing: content-box;
            border: 4px solid rgba(76, 175, 80, .5);
        }
        
        .icon-fix {
            top: 8px;
            width: 5px;
            left: 26px;
            z-index: 1;
            height: 85px;
            position: absolute;
            transform: rotate(-45deg);
            background-color: #FFFFFF;
        }
    }

    @keyframes rotate-circle {
      0% {
          transform: rotate(-45deg);
      }
      5% {
          transform: rotate(-45deg);
      }
      12% {
          transform: rotate(-405deg);
      }
      100% {
          transform: rotate(-405deg);
      }
  }

  @keyframes icon-line-tip {
      0% {
          width: 0;
          left: 1px;
          top: 19px;
      }
      54% {
          width: 0;
          left: 1px;
          top: 19px;
      }
      70% {
          width: 50px;
          left: -8px;
          top: 37px;
      }
      84% {
          width: 17px;
          left: 21px;
          top: 48px;
      }
      100% {
          width: 25px;
          left: 14px;
          top: 45px;
      }
  }

  @keyframes icon-line-long {
      0% {
          width: 0;
          right: 46px;
          top: 54px;
      }
      65% {
          width: 0;
          right: 46px;
          top: 54px;
      }
      84% {
          width: 55px;
          right: 0px;
          top: 35px;
      }
      100% {
          width: 47px;
          right: 8px;
          top: 38px;
      }
    }
  }
}