.team {
  padding: 0px 0 32px;

  &__header {
    width: 100%;
    white-space: nowrap;
    overflow: hidden;
    padding: 60px 0;
    margin-bottom: 140px;
    background-color: lightgreen;
    font-weight: 800;
    font-size: 80px;
    line-height: 98px;
    letter-spacing: 0.01em;
    text-transform: uppercase;
    color: color(black);

    @keyframes marquee {
      0%   { transform: translate(0, 0); }
      100% { transform: translate(-100%, 0); }
    }

    span{
      display: inline-block;
      padding-left: 100%;
      animation: marquee 30s linear infinite;

      &:hover{
        animation-play-state: paused;
      }
    }

    @include pcScreen{
      padding: 35px 0;
      margin-bottom: 125px;
      font-size: 64px;
      line-height: 78px;
    }

    @include tabletScreen{
      padding: 16px 0;
      margin-bottom: 62px;
      font-size: 40px;
      line-height: 49px;
    }

    @include phoneScreen{
      margin-bottom: 40px;
      font-size: 32px;
      line-height: 39px;
    }
  }

  &__title {
    color: black;
    margin: 0 0 40px;
    font-size: 50px;
    font-weight: 600;
  }

  &__container {
    display: flex;
    justify-content: space-between;
    color:black;

    @media screen and (max-width:600px){
      flex-direction: column;
      position: relative;
    }
  }

  &__article-box{
    margin-top: 218px;
    margin-right: 50px;
    padding-left: 40px;

    @include pcScreen{
      margin-top: 80px;
      margin-right: 50px;
      padding-left: 20px;
    }

    @include tabletScreen{
      margin-top: 86px;
      margin-right: 10px;
    }

    @include phoneScreen{
      margin-right: 0;
      margin-top: 60px;
      padding: 0;
    }
  }

  &__article {
    max-width: 872px;
    font-weight: 500;
    font-size: 32px;
    line-height: 56px;
    letter-spacing: 0.01em;
    position: relative;

    &::before,&::after{
      content: '';
      position: absolute;
      width: 95px;
      height: 75px;
      @include backgroundImage(cover);
    }

    &::before{
      left: -40px;
      top: -110px;
      background-image: url('.././assets/images/team__quote-left.svg');
    }

    &::after{
      right: -40px;
      bottom: -80px;
      background-image: url('.././assets/images/team__quote-right.svg');
    }

    @include pcScreen{
      max-width: 580px;
      font-size: 22px;
      line-height: 38px;

      &::before,&::after{
        width: 44px;
        height: 35px;
      }
  
      &::before{
        left: -40px;
        top: -45px;
      }
  
      &::after{
        right: -40px;
        bottom: -40px;
      }
    }

    @include tabletScreen{
      max-width: 420px;
      font-size: 16px;
      line-height: 24px;

      &::before,&::after{
        width: 28px;
        height: 22px;
      }
  
      &::before{
        left: -20px;
        top: -35px;
      }
  
      &::after{
        right: -10px;
        bottom: -30px;
      }
    }

    @include phoneScreen{
      margin: 0 auto;
      max-width: 600px;
      font-size: 14px;
      line-height: 24px;
  
      &::before{
        left: 0;
      }
  
      &::after{
        right: 0;
        bottom: -35px;
      }
    }
  }

  &__article-author {
    margin-top: 186px;
    font-weight: 800;
    font-size: 48px;
    line-height: 59px;
    text-transform: capitalize;

    span{
      margin-top: 24px;
      display: block;
      font-weight: 400;
      font-size: 32px;
      line-height: 34px;
      letter-spacing: 0.01em;
      text-transform: none;
    }

    @include pcScreen{
      margin-top: 148px;
      font-size: 32px;
      line-height: 39px;

      span{
        margin-top: 12px;
        font-size: 24px;
        line-height: 34px;
      }
    }

    @include tabletScreen{
      margin-top: 92px;
      font-size: 24px;
      line-height: 29px;

      span{
        margin-top: 4px;
        font-size: 16px;
        line-height: 34px;
      }
    }

    @media screen and (max-width:600px){
      position: absolute;
      bottom: 0;
      margin-top: 20px;
      font-size: 20px;
      line-height: 24px;

      span{
        margin-top: 8px;
        font-size: 14px;
        line-height: 17px;
      }
    }
  }

  &__image {
    margin-top: 40px;
    max-width: 614px;
    width: 100%;
    max-height: 812px;
    @include backgroundImage(contain);
    background-position-x: center;
    background-image: url('../assets/images/team__C.svg');
    z-index: 2;
    position: relative;

    &::after{
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      bottom: 0;
      right: 0;
      @include backgroundImage(contain);
      background-position-x: center;
      background-image: url('../assets/images/team__grudovich.png');
      z-index: -1;
    }

    @include pcScreen{
      margin-top: 30px;
      max-width: 405px;
      max-height: 536px;
    }

    @include tabletScreen{
      margin: 60px 0 0 30px;
      max-width: 310px;
      max-height: 536px;
    }

    @include phoneScreen{
      margin-top: 30px;
      max-width: 232px;
      height: 320px;
    }

    @media screen and (max-width:600px){
      margin: 49px auto 20%;
      max-width: 100%;
      height: 410px;
    }

    @media screen and (max-width: 365px){
      margin-bottom: 7%;
    }
  }
  
  &__members-wrapper {
    margin: 160px auto 0;
    max-width: 1680px;
    height: 740px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: relative;
    overflow: hidden;

    &.slick{
      margin: 0;
      width: .1px;
      height: .1px;
      opacity: 0;
      visibility: hidden;
    }

    @include pcScreen{
      max-width: 1300px;
      margin: 100px auto 0;
      height: 500px;
    }

    @media screen and (max-width:1320px){
      display: none;

      &.slick{
        display: block !important;
        max-width: 1000px;
        margin: 100px auto 0;
        width: 100%;
        height: 500px;
        opacity: 1;
        visibility: visible;

        .slick-center{
          .team__member{
            padding-top: 22.5px;
            margin: 0;
            height: 500px !important;
            background-color: #8BC63E;
            border-color: lightgreen;
            border-color: rgba(238, 255, 216, 0.5);

            .team__member-photo{

              &::before{
                left: 25px;
                top: 10px;
                color: color(black);
              }
      
              &::after{
                right: 0;
                color: color(black)
              }
            }

            .team__member-name{
              bottom: 60px;
            }
          }
        }
      }
    }

    &.slick{
      @include tabletScreen{
        max-width: 744px;
        height: 455px;
        position: relative;

        .slick-center{
          .team__member{
            padding-top: 22.5px;
            height: 400px !important;

            &.two{
  
              .team__member-name{
                bottom: 34px;
              }
            }

            .team__member-name{
              bottom: 60px;

              @include phoneScreen{
                bottom: 40px;
              }
            }
          }
        }
      }
  
      @include phoneScreen{
        margin: 0px auto 0;
        max-width: 320px;
        height: 466px;

        .slick-center{
          .team__member{

            &.two{
  
              .team__member-name{
                bottom: 60px;

                @include phoneScreen{
                  bottom: 40px;
                }
              }
            }
            .team__member-photo{

              &::before{
                left: 15px;
                top: 10px;
                color: black;
              }
            }
          }
        }
      }
    }
  }
  
  &__member {
    width: 24%;
    height: 700px;
    background-color: #F0F3EB;
    border-radius: 10px;
    transition: all .4s;
    border: 8px solid #F0F3EB;
    position: relative;
    overflow: hidden;

    &.two{
      .team__member-photo{
        img{
          object-fit: cover;
        }
      }

      .team__member-name{
        bottom: 50px;
      }
    }

    &:hover{
      width: 26%;
      height: 740px;
      padding-top: 1.2%;
      background-color: lightgreen;
      border-color: lightgreen;
      border-color: rgba(238, 255, 216, 0.5);

      &.two{
  
        .team__member-name{
          bottom: 70px;
        }
      }

      .team__member-photo{
        &::before{
          left: 20px;
        }

        &::after{
          right: 10px;
        }

        &::before,
        &::after{
          color: rgba(0, 0, 0, 0.808);
        }
      }

      .team__member-name{
        bottom: 110px;
      }
    }

    &.one{
      .team__member-photo{

        &::before{
          content: 'O';
        }
  
        &::after{
          content: 'M';
        }
      }
    }

    &.two{
      .team__member-photo{
        &::before{
          content: 'D';
        }
  
        &::after{
          content: 'E';
        }
      }
    }

    &.three{
      .team__member-photo{
        &::before{
          content: 'C';
        }
  
        &::after{
          content: 'E';
        }
      }
    }

    &.four{
      .team__member-photo{

        img{
          object-fit: contain;
        }

        &::before{
          content: 'H';
        }
  
        &::after{
          content: 'R';
        }
      }
    }

    @include pcScreen{
      width: 295px !important;
      height: 455px !important;

      &.two{
  
        .team__member-name{
          bottom: 40px;
        }
      }

      &:hover{
        padding-top: 1.70%;
        height: 500px !important;

        .team__member-photo{
          &::before{
            left: 30px;
          }
  
          &::after{
            right: 20px;
          }
        }

        &.two{
  
          .team__member-name{
            bottom: 62.5px;
          }
        }

        .team__member-name{
          bottom: 62.5px;
        }
      }
    }

    @media screen and (max-width:1320px){
      margin-top: 22.5px;

      &:hover{
        padding-top: 0;
        height: 455px !important;
        background-color: #F0F3EB;
        border-color: #F0F3EB;

        .team__member-photo{
          &::before{
            left: -100%;
          }
  
          &::after{
            right: -100%;
          }
        }

        &.two{
  
          .team__member-name{
            bottom: 40px;
          }
        }

        .team__member-name{
          bottom: 40px;
        }
      }
    }

    @include tabletScreen{
      width: 232px !important;
      height: 360px !important;
      margin-top: 20px;
      border-width: 4px;

      &.two{
  
        .team__member-name{
          bottom: 15px;
        }
      }

      &:hover{
        margin-top: 20px;
        border-width: 4px;
        padding-top: 0;
        height: 360px !important;
        background-color: #F0F3EB;
        border-color: #F0F3EB;

        .team__member-photo{
          &::before{
            left: -100%;
          }
  
          &::after{
            right: -100%;
          }
        }

        &.two{
  
          .team__member-name{
            bottom: 15px;
          }
        }

        .team__member-name{
          bottom: 40px;
        }
      }
    }

    @include phoneScreen{
      width: 300px !important;

      &.two{
  
        .team__member-name{
          bottom: 40px;
        }
      }
    }
  }
  
  &__member-photo{
    margin: 0 auto;
    width: 350px;
    height: 440px;
    position: relative;
    z-index: 2;
    
    &::before,
    &::after{
      position: absolute;
      color: transparent;
      font-weight: 700;
      font-size: 180px;
      z-index: -1;
      transition: all .6s;
    }

    &::before{
      top: 0;
      left: -100%;
    }

    &::after{
      top: 120px;
      right: -100%;
    }

    @include pcScreen{
      width: 265px;
      height: 300px;

      &::before,
      &::after{
        font-size: 120px;
      }

      &::before{
        top: 0px;
      }
  
      &::after{
        top: 80px;
      }
    }

    @include tabletScreen{
      width: 223px;
      height: 243px;

      &::before,
      &::after{
        font-size: 100px;
      }

      &::before{
        top: 0px;
      }
  
      &::after{
        top: 60px;
      }
    }
  }

  &__member-name {
    position: absolute;
    width: 100%;
    bottom: 90px;
    left: 50%;
    transform: translateX(-50%);
    font-weight: 800;
    font-size: 54px;
    line-height: 66px;
    text-align: center;
    text-transform: capitalize;
    color: color(black);
    transition: all .4s;

    span{
      display: block;
      margin-top: 24px;
      font-weight: 300;
      font-size: 30px;
      line-height: 37px;
    }

    @include pcScreen{
      bottom: 40px;
      font-size: 28px;
      line-height: 34px;

      span{
        margin-top: 12px;
        font-size: 18px;
        line-height: 26px;
        letter-spacing: 0.04em;
      }
    }

    @include tabletScreen{
      span{
        margin-top: 8px;
        font-size: 16px;
      }
    }
  }

  .slick-track{
    margin-left: 10px;
  }
  .slick-arrow {
    display: none !important;
    font-size: 0;
  }
}